<template>
  <div>
    <UserHead :src="src" :username="username"></UserHead>
    <div class="content">
      <mu-list>
        <mu-list-item button :ripple="true" @click="changeAvatar">
          <mu-list-item-action>
            <mu-icon value="send"></mu-icon>
          </mu-list-item-action>
          <mu-list-item-title></mu-list-item-title>
        </mu-list-item>
        <mu-list-item button :ripple="true" @click="rmLocalData">
          <mu-list-item-action>
            <mu-icon value="drafts"></mu-icon>
          </mu-list-item-action>
          <mu-list-item-title></mu-list-item-title>
        </mu-list-item>
        <mu-list-item button @click="checkNotice">
          <mu-list-item-action>
            <mu-icon value="email"></mu-icon>
          </mu-list-item-action>
          <mu-list-item-title></mu-list-item-title>
        </mu-list-item>
      </mu-list>
      <!--<mu-divider/>-->
    </div>
    <div class="logout">
      <mu-button
        full-width
        class="demo-raised-button"
        color="error"
        @click="logout"
        ></mu-button
      >
    </div>
    <div style="height:80px"></div>
    <Bottom></Bottom>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { clear, removeItem } from "@utils/localStorage";
import Confirm from "@components/Confirm";
import Alert from "@components/Alert";
import Bottom from "@components/Bottom";
import UserHead from "@components/userHead";
import socket from "../socket";
import loginMixin from '../mixin/login';

export default {
  name: "Home",
  mixins: [loginMixin],
  data() {
    return {};
  },
  async mounted() {
    if (this.isLogin()) {
      const data = await Confirm({
        title: "",
        content: "~",
        ok: "",
        cancel: ""
      });
      if (data === "submit") {
        this.$router.push("/login");
        return;
      }
      this.$router.push("/");
    }
  },
  methods: {
    checkNotice() {

      if (!("Notification" in window)) {
        Alert({
          content: ""
        });
      }

      // 
      else if (Notification.permission === "granted") {
        // If it's okay let's create a notification
        Alert({
          content: ""
        });
      }

      // 
      else if (Notification.permission !== "denied") {
        console.log('');
        Notification.requestPermission(function(permission) {
          // 
          if (permission === "granted") {
            Alert({
              content: ""
            });
          } else {
            Alert({
              content: ""
            });
          }
        });
      } else {
        console.log(Notification.permission);
      }
    },
    changeAvatar() {
      this.$router.push("/avatar");
    },
    async rmLocalData() {
      const data = await Confirm({
        title: "",
        content: ""
      });
      if (data === "submit") {
        removeItem("update-20180916");
      }
    },
    async logout() {
      const data = await Confirm({
        title: "",
        content: ""
      });
      if (data === "submit") {
        socket.emit('roomout');
        clear();
        this.$store.commit("setUserInfo", {});
        this.$store.commit("setUnread", {
          room1: 0,
          room2: 0
        });
        this.$router.push("/");
      }
    },
    handleGithub() {
      Alert({
        content: "https://github.com/hua1995116/webchat"
      });
    },
    handleTips() {
      Alert({
        title: "",
        html:
          '<div><img style="width: 200px" src="//s3.qiufengh.com/money/WechatIMG64.jpeg" /></div>'
      });
    }
  },
  computed: {
    ...mapState({
      username: state => state.userInfo.userid,
      userid: state => state.userInfo.id,
      src: state => state.userInfo.src
    })
  },
  components: {
    Bottom,
    UserHead
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

.logout {
  margin: 0 20px;
}
</style>
